  border-color: #999;
}
.weui-uploader__input-box:active:after,
.weui-uploader__input-box:active:before {
  background-color: #999;
}
.weui-uploader__input {
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
